import { createStore } from 'vuex';

const store = createStore({
  state: {
    isMobile: window.innerWidth <= 768, // Initialiser avec la taille actuelle de la fen√™tre
  },
  mutations: {
    setIsMobile(state, value) {
      state.isMobile = value;
    },
  },
  actions: {
    updateScreenSize({ commit }) {
      const isMobile = window.innerWidth <= 768; // V√©rifier la taille actuelle de la fen√™tre
      commit('setIsMobile', isMobile); // Mettre √† jour l'√©tat dans Vuex
    },
  },
  getters: {
    isMobile: (state) => state.isMobile, // Getter pour r√©cup√©rer isMobile
  },
});

export default store;

// Dans ton fichier principal, assure-toi de bien √©couter les redimensionnements
if (typeof window !== 'undefined') {
  window.addEventListener('resize', () => {
    store.dispatch('updateScreenSize'); // Mettre √† jour l'√©tat chaque fois que la fen√™tre est redimensionn√©e
  });
}